:root {
  --background: #f2f2f2;
  --foreground: #000000;
  --accent: #ff0000;
  --secondary: #666666;
  --border: #e5e5e5;
  --hover: #f5f5f5;
  --anim--hover-time: 500ms;
  --anim--hover-ease: cubic-bezier(0.25, 1, 0.5, 1);
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: "Fira Code", monospace;
  margin: 0;
  padding: 0;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.header-banner {
  background: #000000;
  padding: 12px 20px;
  width: 100%;
}
.project-title {
  color: #ffffff;
  font-size: 24px;
  font-weight: bold;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
  white-space: nowrap;
}
.nav-buttons {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}
.nav-button {
  padding: 8px 12px;
  font-size: 10px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ffffff;
  border: 1px white;
  cursor: pointer;
  transition: opacity 0.2s ease;
  white-space: nowrap;
  background-size: 4px 4px;
}
.nav-button:hover {
  opacity: 0.8;
}
.contract-address {
  background: #000000;
  border: 1px solid #ffffff;
  padding: 8px 12px;
  color: #ffffff;
  font-family: "Courier New", monospace;
  font-size: 10px;
  line-height: 1.2;
  white-space: nowrap;
}
.clean-button {
  background: transparent;
  border: 2px solid var(--foreground);
  color: var(--foreground);
  padding: 12px 24px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  text-decoration: none;
  display: inline-block;
  text-align: center;
}
.clean-button:hover {
  background: var(--foreground);
  color: var(--background);
}
.clean-button.primary {
  background: var(--foreground);
  color: var(--background);
}
.clean-button.primary:hover {
  background: var(--secondary);
  border-color: var(--secondary);
}
.nav-link {
  color: var(--foreground);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
}
.nav-link:hover {
  color: var(--secondary);
}
.terminal-footer {
  background: #f8f8f8;
  border-top: 1px solid var(--border);
  padding: 16px 0;
}
.terminal-link {
  color: var(--foreground);
  text-decoration: none;
  font-family: "Courier New", monospace;
  font-size: 12px;
  margin-right: 24px;
  white-space: nowrap;
  transition: color 0.2s ease;
}
.terminal-link:hover {
  color: var(--accent);
}
.scrollbar-none::-webkit-scrollbar {
  display: none;
}
.scrollbar-none {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
@media (max-width: 768px) {
  .header-banner {
    padding: 16px;
  }
  .header-banner > div {
    flex-direction: column;
    gap: 12px;
  }
  .nav-buttons {
    justify-content: center;
  }
  .project-title {
    font-size: 20px;
  }
  .clean-button {
    font-size: 12px;
    padding: 8px 16px;
  }
  .terminal-link {
    font-size: 10px;
    margin-right: 16px;
  }
}
@keyframes breathing {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.6;
    transform: scale(1.05);
  }
}
.breathing {
  animation: breathing 2s ease-in-out infinite;
}
